.lyricsArrow {
	width:0;
	height:0;
	padding:0;
	margin:0 auto;
	border-color:transparent transparent rgb(0, 30, 0);
	border-style:solid;
	border-width:0.5em
}

.lyrics {
	border:0.3em solid rgb(0, 30, 0);
	padding:1em .5em;
	margin-top:-1px
}

.rC {
	margin:.5em auto
}

.rC.instagram,
.rC.imgur {
	max-width:37em
}

.rC.fixed {
	position:fixed;
	z-index:10000;
	top:0;
	left:0;
	width:100%;
	max-width:100%;
	margin:0;
	padding:1px;
	background:black;
	border-bottom:.15em solid white
}

.bright .rC.fixed {
	background:white;
	border-color:black
}

.rC>.rSC {
	position:relative;
	height:0;
	padding-bottom:56.25%
}

.rC.soundcloud>.rSC {
	padding-bottom:20%
}

.rC.instagram>.rSC {
	padding-bottom:246%;
	overflow:auto
}

.rC.imgur>.rSC {
	padding-bottom:220%;
	overflow:auto
}

.rC.tiktok>.rSC {
	height:895px;
	padding-bottom:0;
	overflow-x:hidden;
	overflow-y:scroll
}

.rC>.rSC>* {
	position:absolute;
	border:0;
	width:100%;
	height:100%;
	max-width:100%;
	max-height:100%;
	left:0;
	top:0;
	overflow-x:hidden;
	overflow-y:hidden
}

.rC>.rSC>.center {
	position:absolute;
	text-align:center;
	width:100%;
	height:100%;
	max-width:100%;
	max-height:100%;
	left:0;
	top:0;
	overflow-x:hidden;
	overflow-y:hidden
}

.rC>.rSC>.center>iframe {
	position:absolute;
	border:0;
	width:100%;
	height:100%;
	max-width:100%;
	min-height:100%;
	left:0;
	top:0;
	overflow-x:hidden;
	overflow-y:auto
}

.rC>.rSC>.center>img {
	display:inline-block;
	max-width:100%;
	max-height:100%
}

.rC>.pc {
	text-align:center;
	font-size:10px
}

